@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap&subset=latin-ext);
html {
  font-family: 'Lato', sans-serif;
  font-size: 16px; }
  @media (max-width: 641px) {
    html {
      font-size: 10px; } }

body {
  margin: 0;
  padding: 0; }

.App {
  align-items: center;
  background-image: url(/Weather-Application/static/media/weather.0b0fc0e7.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  overflow: hidden;
  width: 100vw; }
  @media (max-width: 641px) {
    .App {
      height: 90vh; } }

.Form__input {
  background: #666699;
  border: 2px solid #666699;
  color: white;
  font-weight: 600;
  font-size: 1.5em;
  margin: 1rem;
  padding: 1rem; }
  @media (max-width: 641px) {
    .Form__input {
      background: whitesmoke;
      border: none;
      color: #444;
      font-size: 2rem; } }

.Form__button {
  background: #666699;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  height: 4rem;
  padding: 1rem; }
  @media (max-width: 641px) {
    .Form__button {
      display: none; } }

@media (max-width: 641px) {
  .Form {
    display: flex;
    flex-direction: column;
    margin: 1rem; } }

.Content {
  -webkit-animation: active 1s linear;
          animation: active 1s linear;
  background: rgba(0, 0, 0, 0.5);
  color: whitesmoke;
  padding: 0 3rem 2rem 3rem; }
  .ContentTemp {
    display: flex;
    justify-content: space-around; }
    .ContentTemp__temperature {
      font-size: 5rem;
      font-weight: 400;
      margin: 0;
      padding-right: 1rem; }
  .Content__icon {
    padding-right: 1rem;
    color: coral; }

@-webkit-keyframes active {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes active {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

